.scheduling-window {
  background: var(--bg-card);
  color: var(--text-main, #fff);
  border-radius: 16px;
  box-shadow: 0 2px 16px rgba(80,120,200,0.07);
  max-width: 100vw;
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.scheduling-window.mobile {
  border-radius: 0;
  min-height: 100vh;
  max-width: 100vw;
  padding-top: 56px;
}
.scheduling-window-back {
  background: none;
  border: none;
  color: var(--text-main, #fff);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.scheduling-window-section {
  font-size: 17px;
  margin-bottom: 8px;
}
.scheduling-window-blocks {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.scheduling-window-actions {
  margin-top: 22px;
}
.scheduling-window-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #888;
  font-size: 18px;
  padding: 24px;
}
